/* For desktop: */
.col-1 {
  width: 8.33%;
}
.col-2 {
  width: 16.66%;
}
.col-3 {
  width: 25%;
}
.col-4 {
  width: 33.33%;
}
.col-5 {
  width: 41.66%;
}
.col-6 {
  width: 50%;
}
.col-7 {
  width: 58.33%;
}
.col-8 {
  width: 66.66%;
}
.col-9 {
  width: 75%;
}
.col-10 {
  width: 83.33%;
}
.col-11 {
  width: 91.66%;
}
.col-12 {
  width: 100%;
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  [class*="col-"] {
    width: 100%;
  }

  .aboutImage img {
    height: auto;
    width: auto;
  }
}

.navbar-content nav {
  /* background-color: rgb(71, 110, 226); */
  background-color: #013a74;
}

img {
  max-width: 100%;
}

.navbar-content img {
  height: auto;
  width: auto;

  padding-right: 1em;
}

* {
  font-family: "Lato", sans-serif;
}

.aboutImage img {
  height: auto;
  width: auto;
  border-radius: 20px;
}

.button {
  margin: 1em;
}
.aboutImage p {
  color: rgb(82, 81, 81);
  font-style: italic;
  font-size: small;
  text-align: left;
}

.aboutBody {
  padding: 140px 0;
}
.aboutBody p {
  font-size: 20px;
  color: #353636;
}

p {
}
.aboutBody button {
  padding-right: 3em;
  margin-right: 1em;
  margin: 5px;
}
.aboutBody a {
  color: white;
  text-decoration: none;
}

.skillsetLogo img {
  width: 130px;
  height: auto;
  margin: 1em;
}

.skillsetLogo li {
  margin: 0.5em;
  font-size: 1.3em;
}

.skillsetLogo h1 {
  border: black solid 1px;
}

.footer {
  background-color: #013a74;

  position: sticky;

  bottom: 0;
  left: 0;
  right: 0;
  height: 40px;
}

.footerContent p {
  text-align: center;
  color: white;
}

.navbar-brand {
  border: black;
  border-radius: 1px;
}
